<template>
  <div class="myLike">
    <van-nav-bar title="我的收藏" left-text="返回" @click-left="goBack" />
    <div v-if="likeData.length > 0">
      <div class="commodity fl">
        <ul class="clearFix">
          <li class="fl" v-for="(item , index) in likeData" :key="index" @click="goPage(item.pid)">
            <div class="imgBox fl">
              <img class="auto-img smallImg" v-lazy="item.smallImg" alt />
            </div>
            <div class="title fl">
              <span class="name">{{item.name}}</span>
              <span class="enname">{{item.enname}}</span>
              <span class="price">￥{{item.price}}</span>
              <span class="icon" @click.stop="notLike(item.pid,index)">
                <img
                  class="auto-img"
                  :src="require('../assets/images/'+[isLike ? 'like-active.png':'like.png'])"
                  alt="图片未加载..."
                />
              </span>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div v-else>
      <van-empty class="v-tip" description="这里啥都没有哦" />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      likeData: [],
      isLike: true
    };
  },
  created() {
    this.getLikeData();
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    // 获取收藏的数据
    getLikeData() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      let tkStr = localStorage.getItem("__Tk");
      this.axios({
        method: "get",
        url: "/findAllLike",
        params: {
          appkey: this.appkey,
          tokenString: tkStr
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 2000) {
            this.likeData = result.data.result;
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    // 取消收藏
    notLike(pid, index) {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      let tkStr = localStorage.getItem("__Tk");
      this.axios({
        method: "post",
        url: "/notlike",
        data: {
          appkey: this.appkey,
          tokenString: tkStr,
          pid
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 900) {
            this.$toast(result.data.msg);
            this.likeData.splice(index, 1);
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    goPage(pid) {
      this.$router.push({ name: "Detail", query: { pid } });
    }
  }
};
</script>

<style lang="less" scoped>
.myLike {
  /deep/ .van-nav-bar__text {
    color: slateblue;
    border-left: 1px dashed #eee;
    border-top: 1px dashed #eee;
    border-radius: 13px;
    padding: 2px 8px;
    box-shadow: 2px 2px 2px #ddd;
    line-height: 25px;
  }
  .commodity {
    padding: 10px 5px;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    overflow-y: auto;
    &::-webkit-scrollbar {
      display: none;
    }
    ul {
      width: calc(~"100% - 10px");
      height: 100%;
      padding: 0 5px 10px 0;
      box-sizing: border-box;
      li {
        border-radius: 10px;
        width: 100%;
        height: 90px;
        background-color: #fff;
        box-shadow: 2px 2px 8px #ccc;
        margin: 0 0 10px 5px;
        padding: 5px 0 5px 5px;
        display: flex;
        .imgBox {
          width: 90px;
          height: 90px;
          .smallImg {
            border-radius: 4px;
            width: 90px;
            height: 90px;
          }
        }
        .title {
          display: flex;
          flex-wrap: wrap;
          margin-left: 10px;
          box-sizing: border-box;
          position: relative;
          width: calc(~"100% - 100px");
          span {
            display: block;
            width: 100%;
            margin: 5px 0 0 0;
            font-size: 16px;
            line-height: 1.5;
            &.enname {
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
              font-size: 14px;
              color: #666;
            }
            &.price {
              width: 50%;
              color: slateblue;
              font-weight: bold;
            }
            &.icon {
              position: absolute;
              bottom: 5px;
              right: 10px;
              width: 28px;
              height: 28px;
              img {
                height: 100%;
              }
            }
          }
        }
      }
    }
  }
  .v-tip{
    height: 550px;
  }
}
</style>